import React from "react"
import { Link } from "gatsby"
import { rhythm } from "../utils/typography"
import Twitter from "./twitter.js"
import GitHub from "./github.js"
import "./navbar.css"

const NavBar = ({location}) => {
  const rootPath = `${__PATH_PREFIX__}/`
  const aboutClass = location.pathname === rootPath ? "navlink navlink-active" : "navlink"
  return (
    <div
      style={{
        display: `flex`,
        justifyContent: "space-evenly",
        marginBottom: rhythm(2.5),
      }}
    >
      <a className="navlink" target="_blank" rel="noopener noreferrer" href="https://twitter.com/croloris">
        <Twitter/>
      </a>

      <Link to="/" className={aboutClass}>
        Home
      </Link>
      <Link to="/blog" className="navlink" activeClassName="navlink-active" partiallyActive={true}>
        Blog
      </Link>
      <Link to="/more" className="navlink" activeClassName="navlink-active">
        More
      </Link>

      <a className="navlink" target="_blank" rel="noopener noreferrer" href="https://github.com/kristoff-it">
        <GitHub/>
      </a>
    </div>
  )
}

      // <a href="/loris-cro-cv.pdf" target="_blank" className="navlink">
      //   CV
      // </a>
export default NavBar
